<template>
  <div
    class="pa-4"
    style="height: 100%;"
    :style="isDarkMode ? 'background-color: #212121;' : 'background-color: #F9F9F9;'"
  >
    <h1>Code Highlight</h1>
    <p>
      参考 <a href="https://www.npmjs.com/package/@highlightjs/vue-plugin/v/1.0.2">https://www.npmjs.com/package/@highlightjs/vue-plugin/v/1.0.2</a>
    </p>
    <p>
      TODO : 自定义高亮语言、主题
    </p>
    <hr class="my-2">

    <highlightjs
      autodetect
      code="var x = 5;"
    />
    <hr class="my-2">

    <highlightjs
      language="javascript"
      :code="code"
    />
  </div>
</template>

<script>

export default {
  components: {
  },
  computed: {
    isDarkMode() {
      return this.$vuetify.theme.dark;
    },
    code() {
      return `// 对象拷贝
function copyObject(orig) {
  var copy = Object.create(Object.getPrototypeOf(orig));
  copyOwnPropertiesFrom(copy, orig);
  return copy;
}

function copyOwnPropertiesFrom(target, source) {
  Object
    .getOwnPropertyNames(source)
    .forEach(function (propKey) {
      var desc = Object.getOwnPropertyDescriptor(source, propKey);
      Object.defineProperty(target, propKey, desc);
    });
  return target;
}`;
    },
  }
};
</script>
